<template>
  <div class="basicExtend">
    <div class="form-group">
      <label>startMix</label>
      <div class="input-item">
        <button @click.stop="startMix()">startMix</button>
      </div>
    </div>
    <div class="form-group">
      <label>hello</label>
      <div class="input-item">
        <button @click.stop="hello()">hellos</button>
      </div>
    </div>
    <div class="form-group">
      <label>Filter</label>
      <div class="input-item">
        <p>{{message2 | capitalize | capitalize2}}</p>
      </div>
    </div>
    <p>1. created --> [1, -> 2] --> </p>
    <p>2. datacreated > 2 > 1 >  </p>
    <p>3.  methodscomputed, components, filters  directives, created,</p>
    <p>4. ""</p>
    <p>5. watch</p>
    <p>6. created</p>
    <p>7. VueVue.config.optionMergeStrategies.methods
    VueVue.config.optionMergeStrategies.newOption = {...} 
      Vue.config.optionMergeStrategies.newOption = Vue.config.optionMergeStrategies.methods</p>
    <p>{{showData}}</p>
  </div>
</template>

<script>
import Vue from 'vue'

Vue.filter('globalFilter', function (value) {
  if (!value) return ''
  return value + ' global'
})

// 
var localMix3 = {
  data () {
    return {
      msg5: 'localMix3-msg5'
    }
  },
  created: function () {
    console.log(': localMix3 --- created')
  }
}
var localMix1 = {
  data () {
    return {
      msg2: 'localMix1',
      msg3: 'localMix1-msg3',
      numList: [7, 8],
      site: {
        name: '',
        url: 'www.taobao.com',
        title: ''
      }
    }
  },
  mixins: [localMix3],
  created: function () {
    console.log(': localMix1 --- created')
  },
  methods: {
    startMix: function () {
      console.log(': localMix1 --- startMix')
    },
    hello: function () {
      console.log(': localMix1 --- hello')
    }
  },
  filters: {
    capitalize2: function (value) {
      if (!value) return ''
      return value + ' localMix1!!'
    }
  }
}
var localMix2 = {
  data () {
    return {
      msg2: 'localMix2',
      msg3: 'localMix2-msg3',
      msg4: 'localMix2-msg3',
      msg5: 'localMix2-msg5',
      numList: [4, 5, 9],
      site: {
        name: '',
        url: 'www.alibaba.com',
        title: '',
        haha: 'haha localMix2'
      }
    }
  },
  created: function () {
    console.log(': localMix2 --- created')
  },
  methods: {
    startMix: function () {
      console.log(': localMix2 --- startMix')
    },
    hello: function () {
      console.log(': localMix2 --- hello')
    }
  }
}

// 
Vue.mixin({
  data () {
    return {
      msg2: '',
      msg3: '-msg3',
      msg4: '-msg4',
      numList: [2, 3],
      site: {
        name: '',
        url: 'www.tenant.com',
        haha: 'haha '
      }
    }
  },
  created: function () {
    console.log(' --- created')
  },
  methods: {
    startMix: function () {
      console.log(' --- startMix')
    },
    hello: function () {
      console.log(' --- hello')
    }
  }
})

export default {
  name: 'basicExtend',
  data () {
    return {
      message: 'hello vue ',
      msg2: 'basic',
      numList: [1, 2],
      site: {
        name: ''
      },
      message2: 'hello Vue extend'
    }
  },
  mixins: [localMix2, localMix1],
  created: function () {
    console.log('basicExtend --- created')
    console.log(this.$options)
  },
  methods: {
    startMix: function () {
      console.log('basicExtend --- startMix')
    }
  },
  computed: {
    showData: function () {
      return JSON.stringify(this.$data)
    }
  },
  filters: {
    capitalize: function (value) {
      if (!value) return ''
      var tmpvalues = value.toString().split(' ').map(function (item) {
        return item.charAt(0).toUpperCase() + item.slice(1)
      })
      return tmpvalues.join(' ')
    }
  }
}
</script>

<style scoped>
  .form-group {
    width: 600px;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    font-size: 16px;
  }
  .form-group > label {
    width: 25%;
    margin-right: 20px;
    color: #666;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  .form-group > label.for-input:after {
    content: ":";
  }
  .form-group > .input-item {
    width: 75%;
    color: #666;
    line-height: 32px;
  }
  .form-group .input-item > input {
    width:90%;
    height: 32px;
  }
  .form-group .input-oper {
    color: #009973;
    cursor: pointer;
    margin-right: 10px;
  }
  .form-group .input-item > button {
    background: #3cc;
    padding: 10px 30px;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
  }
</style>



